  font-family: 'Open Sans', sans-serif; }

.socials {
  padding-top: 33px;
  float: right; }

.socials a {
  background: url(../images/time_bg.png) 0 center repeat-x;
  float: left;
  display: block;
  font-size: 20px;
  width: 40px;
  height: 37px;
