.copy {
  float: left;
  padding-top: 44px;
  font-size: 10px;
  line-height: 18px;
  text-align: left;
  text-transform: uppercase; }

#toTop {
  display: none;
  text-decoration: none;
  position: fixed;
  bottom: 40px;
  left: 51%;
  margin-left: 500px;
  overflow: hidden;
  width: 50px;
  height: 49px;
  border: none;
  text-indent: -999px;
  z-index: 20;
  background: url(../images/totop.png) no-repeat left 0;
  transition: 0s ease;
  -o-transition: 0s ease;
  -webkit-transition: 0s ease; }

#toTop:hover {
  outline: none;
  background-position: right 0; }

/*==================================RESPONSIVE LAYOUTS===============================================*/
@media only screen and (max-width: 995px) {
  body {
    min-width: 768px; }
  .fl1 {
    float: none;
    margin-left: 0 !important;
    width: auto; }
  .map figure, .map figure iframe, #form input, #form textarea, #form .success {
    width: 100% !important;
    float: none !important; }
  #form .success {
    -webkit-box-sizing: border-box;
    -o-box-sizing: border-box;
    box-sizing: border-box; }
  .map figure {
    height: auto !important; }
  .extra_wrapper {
    overflow: visible; }
  .map figure {
    margin-bottom: 15px; }
  .nowrap {
    white-space: normal; }
  .caption .price {
    display: none; }
  .caption {
    padding-top: 20px; }
  .camera_prev {
    margin-left: -374px; }
  .camera_next {
    margin-right: -374px; }
  .fl1.fl2 {
    clear: both; }
  .goToTop {
    margin-top: -5%; } }

@media only screen and (max-width: 767px) {
  body {
    min-width: 420px; }
  header {
    -ms-background-size: auto auto !important;
    background-size: auto auto !important; }
  #bookingForm {
    margin-right: 0; }
  .tmRadio p {
    padding-left: 0;
    padding-top: 10px; }
  .tmRadio {
    margin-left: 0 !important; }
  .tmRadio strong {
    margin-left: 0; }
  #bookingForm > strong + .controlHolder {
    width: 280px; }
  .camera_prev {
    margin-left: 0;
    left: 20px;
    bottom: 60px;
    top: auto;
    margin-top: 0; }
  .camera_next {
    margin-right: 0;
    right: 20px;
    bottom: 60px;
    top: auto;
    margin-top: 0; }
  .block1 {
    margin-bottom: 30px; }
  .map address + address {
    margin-left: 0;
    float: none;
    margin-top: 20px; }
  .socials {
    float: none;
    overflow: hidden; }
  .copy {
    float: none;
    padding-top: 40px; }
  header h1 {
    display: block;
    width: auto;
    position: relative;
    height: auto;
    margin-bottom: 20px; }
  .img_inner {
    width: 100% !important;
    float: none !important;
    margin-right: 0 !important;
    margin-bottom: 20px !important; }
  h1 a {
    max-width: 90%; }
  .img_inner img {
    width: 100%; }
  header h1 {
    float: none;
    text-align: center; }
  header h1 a {
    width: auto;
    display: inline-block;
    height: auto; }
  .map {
    padding-right: 0px !important; }
  .map figure iframe {
    width: 100%;
    height: 300px; }
  .map address {
    margin-right: 0; }
  .noresize {
    width: auto !important;
    float: left !important;
    margin-right: 20px !important;
    margin-top: 4px !important; }
  .img_inner {
    margin-top: 30px; }
  .map address {
    float: none; }
  .ui-datepicker {
    width: 280px; }
  .goToTop {
    margin-top: -5%; } }

@media only screen and (max-width: 767px) and (max-width: 479px) {
  body {
    min-width: 300px; }
  #bookingForm > strong + .controlHolder {
    width: 210px; }
  .ui-datepicker {
    left: 50% !important;
    margin-left: -140px; } }

